import React, {useState} from 'react';
import {ProfileContainer , CardContainer, FrontSideContainer, BackSideContainer, ParagraphContainer, TitleContainer, ImageContainer, StyledHR, Logo, FlippyContainer, LogosContainer, ContentContainer} from './profile.styles'
import picture from '../../assets/picture.jpg'
import javascript from '../../assets/javascript.svg'
import python from '../../assets/python.svg'
import react from '../../assets/react.svg'
import sql from '../../assets/sql.svg'
import java from '../../assets/java.svg'
import kubernates from '../../assets/kubernates.svg'
import docker from '../../assets/docker.svg'
import aws from '../../assets/aws.svg'
import git from '../../assets/git.svg'
import express from '../../assets/express.svg'
import mongodb from '../../assets/mongodb.svg'
import github from '../../assets/github2.svg'
import graphql from '../../assets/graphql.svg'
import nodejs from '../../assets/nodejs.svg'
import linkedin from '../../assets/linkedin.svg'
import mail from '../../assets/mail.svg'

const Profile = () => {
    const [mailView, setMailView] = useState(false)
    return (
        <ProfileContainer>
            <CardContainer>
                <FlippyContainer style={{ width: '30vw', height: '70vh' }} flipOnClick={true} flipDirection="vertical">   
                    <FrontSideContainer reversed>
                        <TitleContainer>ABOUT ME</TitleContainer>
                        <StyledHR/>
                        <ImageContainer>
                            <img src={picture} width='50%' height='50%' alt=''/>
                        </ImageContainer>
                        <ParagraphContainer>행복한 개발자입니다</ParagraphContainer>
                    </FrontSideContainer>
                    <BackSideContainer reversed>
                        <TitleContainer>Strengths</TitleContainer>
                        <StyledHR/>
                        <ContentContainer>
                            <TitleContainer>Ambitious</TitleContainer>
                            <ParagraphContainer>꿈은 오늘의 나를 행복하게 합니다</ParagraphContainer>
                            <TitleContainer>Broad-minded</TitleContainer>
                            <ParagraphContainer>창의성은 다양성에서 비롯됩니다</ParagraphContainer>
                            <TitleContainer>Curious</TitleContainer>
                            <ParagraphContainer>도전은 즐겁고, 배움은 도전의 과정이자 목표입니다</ParagraphContainer>
                        </ContentContainer>
                        
                    </BackSideContainer>
                </FlippyContainer>
            </CardContainer>
            <CardContainer>
                <FlippyContainer style={{ width: '30vw', height: '70vh' }} flipOnClick={true} flipDirection="horizontal">   
                    <FrontSideContainer> 
                        <TitleContainer>SKILLSET</TitleContainer>
                        <StyledHR/>
                        <ContentContainer>
                            <ParagraphContainer>Confident In</ParagraphContainer>
                            <ParagraphContainer><Logo src={javascript}/><Logo src={react}/><Logo src={python}/></ParagraphContainer> 
                            <ParagraphContainer>Comfortable With</ParagraphContainer>
                            <ParagraphContainer><Logo src={nodejs}/><Logo src={mongodb}/><Logo src={express}/></ParagraphContainer>
                            <ParagraphContainer><Logo src={git}/><Logo src={github}/><Logo src={sql}/><Logo src={java}/></ParagraphContainer>
                            <ParagraphContainer>Interested In</ParagraphContainer>
                            <ParagraphContainer><Logo src={graphql}/><Logo src={aws}/><Logo src={docker}/><Logo src={kubernates}/></ParagraphContainer>
                        </ContentContainer>
                        
                    </FrontSideContainer>
                    <BackSideContainer>
                        <TitleContainer>EDUCATION</TitleContainer>
                        <StyledHR/>
                        <ParagraphContainer>숭실대학교 소프트웨어학부 7학기 재학중</ParagraphContainer>
                        <ParagraphContainer>정보처리기사 취득</ParagraphContainer>
                        <ParagraphContainer>TOEIC 930, OPIC IH</ParagraphContainer>
                    </BackSideContainer>
                </FlippyContainer>
            </CardContainer>
            <LogosContainer mailView={mailView}>
                
                <Logo src={mail} onClick={()=>setMailView(prev=>!prev)}/>
                {mailView && <p>wer153@naver.com</p>}
                <Logo src={github} onClick={()=>window.open('https://github.com/wer153')}/>
                <Logo src={linkedin} onClick={()=>window.open('https://www.linkedin.com/in/minki-kim-219392199/')}/>
            </LogosContainer>
            
            
        </ProfileContainer>
        
    )
}
export default Profile
