import React from 'react'
import { ProjectsContainer, ProjectContainer } from './projects.styles'
import {ReactComponent as Github} from '../../assets/github.svg'

const Projects = (props) => {
    const items = [
        {title:'Clothing Shop', ghlink:'https://github.com/wer153/clothing-shop', link:'https://wer153.github.io/clothing-shop/'},
        {title:'Keeper', ghlink:'https://github.com/wer153/Keeper', link:'https://wer153.github.io/Keeper/'},
        {title:'Mastermind', ghlink:'https://github.com/wer153/Mastermind.io', link:'https://wer153.github.io/Mastermind.io/'},
        {title:'Weather App', ghlink:'https://github.com/wer153/weather-app', link:'https://minki-weather-app.herokuapp.com/'}

    ]
    return (
    <ProjectsContainer>
        {items.map((item,index)=>(
            <ProjectContainer 
                key={index} 
                curr={index===props.id} 
                onMouseOver={()=>props.handleMouseOver(index,item.link)}
                onClick={()=>window.open(item.ghlink)}
            >
                {index===props.id && <Github/>}  {item.title} 
            </ProjectContainer>
        ))}
        
    </ProjectsContainer>)
}

export default Projects