import styled, {css} from 'styled-components'

export const ProjectsContainer = styled.div`
    /* flex:1; */
    
    width:50%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;

    font-size: 28px;
    font-weight: bold;
`

export const ProjectContainer = styled.p`
    color:gray;
    
    &:hover {
        cursor:pointer;
    }

    ${(props)=>props.curr&&css`color:white;`} /* change color on mouse hover */
`