import { FrontSide, BackSide, Flippy } from 'react-flippy'
import styled, {css} from 'styled-components'


export const ProfileContainer = styled.div` 
    display: flex;
    width: 100vw;
    height: 100vh;
    position: relative;
    justify-content: center;
    
    align-items:center;
    text-align:center;
    background-color:#E7E1D7;
    /* margin: 50px 0px; */
`

export const CardContainer = styled.div` 
    display: flex;
    align-items:center;
    text-align:center;
    margin: 5px;
    
    &:hover{
        cursor:pointer;
    }
    
`

export const FlippyContainer = styled(Flippy)`
    height: 750px;
    
/* style={{ width: '650px', height: '750px' }} /// these are optional style, it is not necessary  */
`

export const FrontSideContainer = styled(FrontSide)`
    
    background-color: #E7E1D7;
    overflow:hidden;
    ${props=>props.reversed && 'background-color: #c6b79f'}
`

export const BackSideContainer = styled(BackSide)`
    background-color: #c6b79f;
    overflow:hidden;
    ${props=>props.reversed && 'background-color: #E7E1D7'}
`

export const ContentContainer = styled.div`
    position: relative;
    margin-top:20%;
    margin-bottom:20%;
`

export const TitleContainer = styled.h3`

    margin-top:5vh;
`

export const ParagraphContainer = styled.p`
    font-size: 17px;
    font-family: 'Nanum Gothic', sans-serif;
    margin-top:1.5vh;
    margin-bottom:1.5vh;
`


export const ImageContainer = styled.div``

export const Logo = styled.img`
    height:3vh;
    margin:0.5vh;
    cursor:pointer;
`
export const StyledHR = styled.hr`
    width: 20%;
    margin-bottom:50px;
    border: 0;
    height: 1px;
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
`

export const LogosContainer = styled.div`
    display: inline-block;
    border-radius: 0.6rem/0.8rem;
    background-color: #c6b79f;
    display:flex;
    flex-flow:column;
    align-items:center;
    padding:10px;
    position:absolute;
    bottom:50%;
    right:20px;
    
`