import React from 'react'
import videofile from '../../assets/seoulcity_originalfile.mp4'
import Typical from 'react-typical'
import {VideoWrapper, VideoContainer, HeadlineContainer, TypicalContainer} from './headline.styles'

const Headline = () => {
    const textKOR = '안녕하세요, 저는 웹 개발자 김민기입니다. '
    const textENG = 'Hello, I am Kim Minki, Web developer. '

    return (
        <HeadlineContainer>
            <VideoWrapper>
                <VideoContainer autoPlay loop width='100%' muted src={videofile} type='video/mp4'/>
            </VideoWrapper>
            <div className='video'>
                
            </div>
            <TypicalContainer>
                <Typical 
                    steps={[textKOR, 3000, textENG, 3000]}
                    loop={Infinity}
                    wrapper='h1'
                />
            </TypicalContainer>
        </HeadlineContainer>
    )
}

export default Headline