import React, { useState } from 'react'
import {PortfolioContainer} from './portfolio.styles'
import Slider from '../slider/slider.component'
import Projects from '../projects/projects.component'

import clothshop from '../../assets/clothing-shop.PNG'
import keeper from '../../assets/keeper.PNG'
import mastermind from '../../assets/mastermind.PNG'
import weatherapp from '../../assets/weatherapp.PNG'

const Portfolio = () => {
    const [slide, setSlide] = useState(clothshop)
    const [ID, setID] = useState(0)
    const [link, setLink] = useState('https://wer153.github.io/clothing-shop/')
    const handleMouseOver = (id,website) => {
        setID(id)
        setLink(website)
        if(id===0){
            setSlide(clothshop)
        }
        if(id===1){
            setSlide(keeper)
        }
        if(id===2){
            setSlide(mastermind)
        }
        if(id===3){
            setSlide(weatherapp)
        }
    }
    return (
        <PortfolioContainer>
            <Projects id={ID} handleMouseOver={handleMouseOver}/>
            <Slider img={slide} link={link}/>
        </PortfolioContainer>
    )   
}

export default Portfolio